@import url(https://fonts.googleapis.com/css2?family=Amatic+SC&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Jolly+Lodger&display=swap);
html {
  box-sizing: border-box; 
  font-size: 16px;
  background-image: url(/chases-epic-story/static/media/halloween3.5e9e57c7.png);
  height: 100%;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  text-align: center;
}

p {
 font-family: 'Amatic SC', cursive;
 color: white;
 font-size: 30px; 
 max-width: 60%;
 margin: auto;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body, h1, h2, h3, h4, h5, h6, p, ol, ul {
  margin: auto;
  padding: 0;
  font-weight: normal;
}
body {
  padding-top: 50px;
}

ol, ul {
  list-style: none;
}

img {
  max-width: 100%;
  height: auto;
}

#main-heading {
  margin-top: 40px;
  margin-bottom: 20px;
  color: red;
  font-family: 'Jolly Lodger', cursive;
  text-align: center;
  font-size: 60px;
}

#options {
    padding-top: 60px;
    margin: auto;
    max-width: 80%;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: space-around;
            justify-content: space-around;
}

button {
  font-family: 'Amatic SC', cursive;
  font-size: 25px;
  color: white;
  background-color: rgba(0, 0, 0, 0.767);
  box-shadow: none;
  outline: none;
  border: solid white 2px;
  padding-top: 8px;
  padding-bottom: 8px;
  padding-left: 10px;
  padding-right: 10px;
}

button:hover {
  box-shadow:

    0 0 80px 30px rgb(203, 235, 235);
}

#story-image {
  margin-top: 40px;
  height: 400px;
  width: 400px; 
  object-fit: cover;
}






